.bcImg-shadow{
  position: absolute;
  width: 100%;
  height: 150%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: -10;
}
#head{
 
  color:black;
  text-align: center;
  
  font-family: Brush Script MT;
  font-size: 40px;
  
}
#root{
  background-color:#c8e6c9 ;
  position: absolute;
  margin: 0;
  top: 50px;
  left: 50%;
  transform: translate(-50%,0);
  border: solid white;
  border-radius:  30px;
  padding-left: 10%;
  padding-right: 10%;
  box-shadow: 10px;
  font-family: Papyrus;
  display: flex;
  flex-direction: column;
  
  
}
input,select,button{
  border: solid black;
  border-radius: 10px;
  background-color: cadetblue;
  font-family: Papyrus;
  font-size:20px;
  width: 100%;
  margin-bottom: 10px;
  text-align: center;
}
label{
  font-family: Papyrus;
  font-size:20px;
  margin-bottom: 10px;
  width: 100%;
}
h2{
  text-align: center;
  
